@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400:700&display=swap');

html[lang="ja"] {
  font-family: "Noto Sans JP", "Hiragino Kaku Gothic ProN", Meiryo, sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.underline {
  border-bottom: 2px solid #00C0AB;
}

.genre_intro {
  width:120px;
  border-radius:12px;
  background-color:rgba(255,255,255,0.25);
  border-width:0;
  margin-right:8px;
  transition: opacity 0.2s;
  height:36px;
  color:white;
  font-weight:bold;
  font-size:15px;
}

.genre_intro:hover {
  background-color:rgba(255,255,255,0.25);
  opacity:0.6
}

.genre_zoom {
  width:120px;
  border-radius:12px;
  background-color:rgba(0,192,171,0.5);
  border-width:0px;
  transition: opacity 0.2s;
  height:36px;
  color:white;
  font-weight:bold;
  font-size:15px;
}

.genre_zoom:hover {
  background-color:rgba(0,192,171,0.5);
  opacity:0.6
}

.date_head {
  color:#00C0AB;
  background-color:rgba(0,0,0,0.7);
  border-radius:8px;
  font-size:8px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom:4px;
  font-weight:bold;
}

.date_box {
  color:white;
  background-color:rgba(0,0,0,0.7);
  border-radius:8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight:bold;
}


@media (max-width:768px) {
  .header_title {
    color:white;
    font-family: 'Noto Sans JP', sans-serif;
    font-weight: bold;
    font-size:32px;
    margin-top:32px;
  }

  .header_caption {
    color:white;
    font-family: 'Noto Sans JP', sans-serif;
    font-size:12px;
    margin-top:16px;
  }

  .genre_fac {
    color:white;
    font-size:12px;
    margin-bottom:0px;
  }

  .genre_intro {
    width:80px;
    height:30px;
    font-size:12px;
  }

  .genre_zoom {
    width:80px;
    height:30px;
    font-size:12px;
  }

  .header_logo {
    height:24px;
    margin-top:16px;
  }

  .date_head {
    width:64px;
    height:16px;
    margin-top:16px;
  }

  .date_box {
    width:64px;
    height:64px;
    font-size:18px;
  }

  .header_height {
    height:450px
  }

  .mode_icon {
    font-size:18px;
  }

  .mode_text {
    color: white;
    font-size:12px;
    font-weight:bold;
    margin-left:6px;
  }

  .main_title {
    text-align:center;
    color:#00C0AB;
    font-size:18px;
    font-weight:bold;
  }

  .main_container {
    margin-top:32px;
  }
  
  .main_top {
    margin-top:32px; 
  }

  .genre_header {
    display: flex;
    justify-content: center;
    margin-top:32px
  }

  .genre_title {
    color:white;
    font-size:16px;
    font-weight:bold;
    margin-left:8px;
  }

  .genre_icon {
    font-size:18px;
  }

  .genre_time {
    color:white;
    font-size:12px;
    margin-bottom:3px;
    margin-right:20px;
  }

  .genre_date {
    color:white;
    font-size:15px;
    font-weight:bold;
    margin-bottom:5px;
  }

  .genre_group {
    color:#00C0ab;
    font-size:15px;
    font-weight:bold;
    margin-bottom:5px;
  }

  .contents_container {
    margin-top:10px;
  }
}



@media (min-width:768px) {
  .header_title {
    color:white;
    font-family: 'Noto Sans JP', sans-serif;
    font-weight: bold;
    font-size:64px;
    margin-top:48px;
  }

  .header_caption {
    color:white;
    font-family: 'Noto Sans JP', sans-serif;
    font-size:16px;
    margin-top:16px;
  }

  .genre_fac {
    color:white;
    font-size:18px;
    margin-bottom:0px;
  }

  .header_logo {
    height:40px;
    margin-top:16px;
  }
  
  .date_head {
    width:80px;
    height:16px;
    margin-top:32px;
  }

  .date_box {
    width:80px;
    height:80px;
    font-size:24px;
  }

  .header_height {
    height:650px
  }

  .mode_icon {
    font-size:24px
  }

  .mode_text {
    color: white;
    font-size:16px;
    font-weight:bold;
    margin-left:8px;
  }

  .main_title {
    text-align:center;
    color:#00C0AB;
    font-size:24px;
    font-weight:bold;
  }

  .main_container {
    margin-top:64px;
  }

  .main_top {
    margin-top:32px; 
  }

  .genre_header {
    display: flex;
    justify-content: center;
    margin-top:48px;
  }

  .genre_title {
    color:white;
    font-size:24px;
    font-weight:bold;
    margin-left:8px;
  }

  .genre_icon {
    font-size:24px;
    margin-top:2px;
  }

  .genre_time {
    color:white;
    font-size:15px;
    margin-right:20px;
    margin-bottom:3px;
  }

  .genre_date {
    color:white;
    font-size:18px;
    font-weight:bold;
    margin-bottom:5px;
  }

  .genre_group {
    color:#00C0ab;
    font-size:18px;
    font-weight:bold;
    margin-bottom:5px;
  }
}

body {
  background-color:#001D1A;
  margin-bottom:100px;
}


.mode_switch {
  transition: opacity 0.2s;
  cursor: pointer;
  cursor: hand;
}

.mode_switch:hover {
  opacity: 0.6; 
}

hr {
  margin:5px;
  margin-left:0px;
  margin-right:0px;
}

.group_filter {
  background-color:transparent;
  border:none;
  outline: none;
  color:white;
  width:300px;
  border-bottom:1px solid #aaaaaa;
  transition: border-bottom 0.2s;
  font-size:18px;
}

.group_filter:focus{
  background-color:transparent;
  border:none;
  outline: none;
  border-bottom:1px solid #ffffff;
}

.search_icon {
  cursor: pointer;
  cursor: hand;
  margin-left:8px;
  font-size:16px;
  transition: opacity 0.2s;
}

.search_icon:hover {
  opacity:60%
}


.accordion_toggle {
  background-color:rgba(0,192,171,0.2);
  cursor: pointer;
  cursor: hand;
  height: 48px;
}

.accordion_collapse {
  background-color:rgba(0,192,171,0.1);
  border-radius: 0px 0px 3px 3px;
}

.date_switch {
  width:120px;
  border-radius:12px;
  background-color:rgba(0,192,171,0.5);
  border-width:0;
  margin-right:8px;
  transition: opacity 0.2s;
  height:64px;
  color:white;
  font-weight:bold;
  font-size:18px;
}


.date_switch_pressed {
  width:120px;
  border-radius:12px;
  background-color:rgba(255,255,255,0.25);
  border-width:0;
  margin-right:8px;
  transition: opacity 0.2s;
  height:64px;
  color:white;
  font-weight:bold;
  font-size:18px;
}

.date_switch:hover {
  opacity:0.6;
}

.date_switch_pressed:hover{
  opacity: 0.6;
}

.timeline {
  overflow: auto;
  white-space: nowrap;
}

.stake {
  height:240px; 
  width:0px;
  background-color:transparent;
  border-right:2px solid rgba(255,255,255,0.5);
}

.stake_container {
  width:30px;
  margin-left:80px;
  margin-right:80px;
}

.stake_text {
  color:white;
  font-weight:bold
}

.time_card {
  width:180px;
  margin-left:5px;
  margin-right:5px;
  border-radius:12px;
  background-color:rgba(0,110,99,1);
  border-width:0px;
  transition: opacity 0.2s;
  height:28px;
  margin-bottom:5px;
  color:white;
  font-weight:bold;
  font-size:12px;
}

.time_card_2p {
  width:180px;
  margin-left:5px;
  margin-right:5px;
  border-radius:12px;
  background-color:rgba(0,82,74,1);
  border-width:0px;
  transition: opacity 0.2s;
  height:28px;
  margin-bottom:5px;
  color:white;
  font-weight:bold;
  font-size:12px;
}

.schedule_text {
  color:white;
  font-weight: bold;
}